    import cj from "@/common/crypto-js.js"
	export default {
		data() {
			return {
				phoneNum: '',
				smsCode: '',
				password: '',
                confirmPwd: '',
				phoneFalg: false,
				codeFalg: false,
				pwdFalg: false,
				beginFalg: false,
                isSame: false,
				sendCodeText: '发送验证码',
                timeText: '',
				num: 60
				
			}
		},
		methods: {
			sendCode(){
                let phone_regExp = /^1[3-9]\d{9}$/;
                if (this.phoneNum == '' || !phone_regExp.test(this.phoneNum)) {
                    uni.showToast({
                        title: '请检查手机号',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false;
                }
			    this.codeTime = setInterval(this.countDown,1000);
                let phoneStr = new Buffer(this.phoneNum).toString('base64');
                uni.request({
                    url: this.websiteUrl + '/api/code/sms',
                    method: 'POST',
                    data: {
                        mobile: phoneStr
                    },
                    success: res => {
                        console.log(res)
                    },
                    fail: () => {},
                    complete: () => {}
                });
			},
			countDown(){
			    this.num--;
			    this.beginFalg = true;
			    this.timeText = this.num + 's';
			    if (this.num <= 0) {
			        this.num = 60;
			        this.timeText = '';
			        this.beginFalg = false;
			        clearInterval(this.codeTime)
			    }
			},
            checkPhone(){
                let phone_regExp = /^1[3-9]\d{9}$/;
                if (this.phoneNum != '' && phone_regExp.test(this.phoneNum)) {
                    this.phoneFalg = true;
                    return true
                } else {
                    this.phoneFalg = false;
                    uni.showToast({
                        title: '请检查手机号',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            checkCode(){
                if (this.smsCode != '') {
                    this.codeFalg = true;
                    return true
                } else {
                    this.codeFalg = false;
                    uni.showToast({
                        title: '验证码不能为空',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            checkPwd(){
                let pwd_regExp = /^(?![a-zA-Z]+$)(?![0-9]+$)[A-Za-z0-9]{6,15}$/;
                if (this.password != '' && pwd_regExp.test(this.password)) {
                    this.pwdFalg = true;
                    return true
                } else {
                    this.pwdFalg = false;
                    uni.showToast({
                        title: '请输入6-15位的字母数字组合',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            checkPwdSame(){
                if (this.confirmPwd != '' && this.password === this.confirmPwd) {
                    this.isSame = true;
                    return true
                } else {
                    this.isSame = false;
                    uni.showToast({
                        title: '两次密码必须相同',
                        icon: 'none',
                        mask: true
                    });
                    setTimeout(function(){
                        uni.hideToast();
                    },2000)
                    return false
                }
            },
            confirm(){
                if (this.checkPhone() && this.checkPwd() && this.checkPwdSame() && this.checkCode()) {
                    let pwdStr = cj.MD5(this.password).toString();
                    let pwdCofStr = cj.MD5(this.confirmPwd).toString();
                    let phoneStr = new Buffer(this.phoneNum).toString('base64');
                    uni.request({
                        url: this.websiteUrl + '/api/forget',
                        method: 'POST',
                        data: {
                            mobile: phoneStr,
                            userPwd: pwdStr,
                            smsCode: this.smsCode,
                            confirmUserPwd: pwdCofStr
                        },
                        success: res => {
                            if (res.data && res.data.status === 0) {
                                uni.showToast({
                                    title: '修改成功',
                                    mask: true
                                });
                                setTimeout(function () {
                                    uni.hideToast();
                                    uni.navigateBack({
                                         delta: 1,
                                         animationType: "slide-in-left"
                                     });
                                },1600)
                            } else {
                                uni.showToast({
                                    title: res.data.message,
                                    icon: 'none',
                                    mask: true
                                });
                                setTimeout(function(){
                                    uni.hideToast();
                                },2000)
                            }
                        },
                        fail: (err) => {},
                        complete: () => {}
                    });
                } else{
                    return false;
                }
            }
		}
	}
